import topFeedBack from '@/components/top-feedBack/index'

export default {
  components: {
    topFeedBack
  },
  data() {
    return {
      showToTopFlag: false
    }
  },
  activated() {
    this.fnScroll()
  },
  beforeRouteLeave(to, from, next) {
    document.getElementById('app-warp').removeEventListener('scroll', this.addEventScrollFn)
    next()
  },
  beforeMount() {
    this.debouncedAction = this.$util.throttle(this.option, 100)
  },
  methods: {
    /**
     * 返回顶部
    */
    fnScrollToTop() {
      // this.$refs.toTop.scrollIntoView({
      //   block: 'start',
      //   behavior: 'smooth', // 平滑过渡
      // }) // 回到顶部
      this.scrollSmoothTo(0)
    },
    /**
     * 隐藏 FeedBack
    */
    hiddenFeedBack() {
      this.showToTopFlag = false
    },
    /**
     * 模拟动画滚动
    */
    scrollSmoothTo(position) {
      console.log('#app-warp scroll')
      if (!window.requestAnimationFrame) {
        window.requestAnimationFrame = function(callback) {
          return setTimeout(callback, 17);
        };
      }
      // debugger
      let _target = document.querySelector('#app-warp')
      // 当前滚动高度
      let scrollTop = _target.scrollTop;
      console.log(scrollTop, 'scrollTop')
      // 滚动step方法
      let step = function () {
        // 距离目标滚动距离
        var distance = position - scrollTop;
        // 目标滚动位置
        scrollTop = scrollTop + distance / 10;
        if (Math.abs(distance) < 1) {
          _target.scrollTo(0, position);
        } else {
          _target.scrollTo(0, scrollTop);
          requestAnimationFrame(step);
        }
      };
      step();
    },
    /**
     * @description 滚动条监控
     */
    fnScroll() {
      document.getElementById('app-warp').addEventListener('scroll', this.addEventScrollFn)
    },
    /**
     * 滚动函数
     */
    addEventScrollFn(ev) {
      this.debouncedAction(ev)
    },
    option(ev) {
      const _scrollTop = ev.target.scrollTop
      this.showToTopFlag = _scrollTop > 200
    }
  },
}
