    </div>
    <!--商品收藏列表为空-->
    <div class="no-data" v-show="parentData.commodityList.length === 0 && isNoData">
      <img :src="`${$cdnImageUrl}/personalCenter/new_collect.png` | imageToHttps" alt="" />
