<script>
/**
 * vant框架
 */
import Vue from 'vue'
import { SwipeCell } from 'vant'
import guessYouLick from '@/components/guessYouLick'
import ProductList from '@/components/publicProductList'
// import ProductList from './publicProductList/index'

Vue.use({ SwipeCell })

/*
 * 我的收藏列表 商品收藏列表
 */
export default {
  name: 'collectionList',
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function() {
        return {}
      },
    },
    canCheck: {
      type: Boolean,
      default: false
    },
    layout: {
      type: Boolean,
      default: false
    }, // false 为列表模式 true 大图模式
    isNoData: {
      type: null,
      default: function() {
        return {}
      },
    },
  },
  data() {
    return {}
  },
  onLoad() {},
  components: {
    guessYouLick,
    publicProductList: ProductList
  },
  methods: {
    log() {
      console.log('parentData')
      console.log(this.parentData)
    },
    /**
     * @description 删除已收藏的商品
     */
    cancelCollections(item) {
      // 回调父组建的取消收藏方法
      this.$emit('cancelCollectionBack', item)
    },

    /**
     * @description  点击跳转到商品详情
     */
    toGoodsDetail(productId) {
      this.$router.push({
        path: `/goodsDetail?productId=${productId}`,
      })
    },
  },
}
</script>

