<template>
  <div class="popup-container" v-show="isShow">
          <div class="popup" @click="popupClick">
            <div class="popup-content">
                <slot></slot>
            </div>
          </div>
  </div>
</template>

<script>
export default {
  name: 'commonPopup',
  model: {
    prop: 'isShow',
    event: 'changeShow'
  },
  props: {
    isShow: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
    }
  },
  methods: {
    // 弹窗背景点击回调
    popupClick(event) {
      if (event.target.className !== 'popup-content' && event.target.className !== 'item item-active' && event.target.className !== 'filter-item-container') {
        this.$emit('bgClicked')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.popup-container{
    transition: all 0.5s;
   .popup{
      position: fixed;
      z-index: 12;
      left: 0;
      top: 2.46rem;
      bottom: 0;
      right: 0;
      background: rgba(0, 0, 0, 0.5);
      .popup-content{
        min-height: 1rem;
        background-color: #fff;
        border-radius: 0 0 .24rem .24rem;
      }
    }
}

</style>
