<template>
  <div class="content" ref="toTop">
   <Header>
      <template v-slot:title >
      <div class="tab-container">
        <div @click="switchTabChange" class="goods" :class="status === '0' && 'checked'" id="0">商品</div>
        <div @click="switchTabChange" class="shops" :class="status === '1' && 'checked'" id="1">店铺</div>
      </div>
      </template>
      <template v-slot:right v-if="status==='0' && data.commodityList.length>0">
        <div class="cert_right_btn" v-if="!isEdit" key="edit" @click="isEdit=true">管理</div>
        <div class="cert_right_btn" v-else key="finish" @click="isEdit=false">完成</div>
      </template>
    </Header>
    <!-- <div class="switch">
      <div class="tab">
        <div @click="switchTabChange" :class="status === '0' && 'checked'" id="0">商品</div>
        <div class="width320"></div>
        <div @click="switchTabChange" :class="status === '1' && 'checked'" id="1">店铺</div>
      </div>
    </div> -->
    <commodity-filter v-if="status === '0'" :parentData="stockNumber" @layoutChange="layoutChange" @chooseStock="chooseStock"></commodity-filter>
    <skeleton v-if="skeletonLoading" :nums="3"/>
    <!--下啦刷新-->
    <van-pull-refresh v-else v-model="isLoading" @refresh="onRefresh">
      <template #pulling><refreshLoading text="下拉刷新"/></template>
        <template #loosing><refreshLoading text="下拉刷新"/></template>
        <template #loading><refreshLoading text="加载中"/></template>
        <template #success><refreshLoading text="加载完成"/></template>
      <!--商品收藏列表-->
      <commodity-list v-show="status === '0'" :parentData = "data" :isNoData="!isLoading && !loading" :canCheck='isEdit' :layout="layoutType" @cancelCollectionBack = "cancelCollection"></commodity-list>

      <!--店铺收藏列表-->
      <div class="shop-content" v-show="status === '1'">
          <!--上啦加载-->
          <van-list
            v-model="loading"
            :finished="finished"
            :finishedText="data.shopList.length > 0 ? '没有更多啦~' : ''"
            @load="onLoad"
            van-clearfix
            :immediate-check="false"
          >
            <!--店铺收藏列表-->
            <shop-list :parentData = "data" :isNoData="!isLoading && !loading" @cancelShopBack = "cancelShop"></shop-list>
          </van-list>
      </div>

    </van-pull-refresh>
    <div class="bottom-option" v-if="isEdit && status==='0'">
      <div class="select-all">
      <common-checkbox v-model="selectAllStatus" class="left" :width="'18px'"  :height="'18px'" @toggleCheck="toggleSelectAll"></common-checkbox><span>全选</span>
      </div>
      <div class="delete-all">
      <span class="delete-btn" @click="deleteMulti">删除</span>
      </div>
    </div>

         <!-- <div class="popup" v-show="hotModuleShowFlag" @touchmove.prevent @click="fnChangeHotModuleShowFlag">
        <ul @click.stop>
          <li :class="{active: qs === 'isHotDesc'}" @click="doFilter('hot')">
            <div class="left">人气优先</div>
            <div class="right">
                <img :src="`${$cdnImageUrl}/search/true.png` | imageToHttps" v-if="qs === 'isHotDesc'" />
                <img :src="`${$cdnImageUrl}/search/grayTrue.png` | imageToHttps" v-if="qs !== 'isHotDesc'" />
            </div>
          </li>
        </ul>
      </div> -->
          <!-- 返回顶部 -->
    <top-feedBack :isShow="showToTopFlag" @hiddenFeedBack="hiddenFeedBack" @toTop="fnScrollToTop"></top-feedBack>
  </div>
</template>

<script>
import commonCheckbox from '@/components/checkbox/common-checkbox'
import skeleton from '@/components/skeleton'
import mixins from '@/components/top-feedBack/mixins'
// import refreshLoading from '@/components/refreshLoading'
/*
 * 店铺收藏列表
*/
// import shopList from './shop-list';

/*
 * 商品收藏列表
*/
import commodityList from './commodity-list';

/*
 * 接口配置文件
*/
import personalApi from '@/http/service/index';
import Header from '@/components/header'
import commodityFilter from './commodity-filter'

/**
 * vant框架
 */
import Vue from 'vue';
import { Toast, List, PullRefresh } from 'vant';
import { mapGetters } from 'vuex'
// import { getSelectBoxTagList } from '@/http/service/search'
const shopList = () => import('./shop-list')
const refreshLoading = () => import('@/components/refreshLoading')
// 全局注册
Vue.use({ Toast, List, PullRefresh });
/*
 * 个人中心-我的收藏页面
*/
export default {
  name: 'myCollectionIndex',
  mixins: [mixins],
  components: {
    skeleton,
    refreshLoading,
    shopList,
    Header,
    commodityList,
    commonCheckbox,
    commodityFilter
  },
  data() {
    return {
      /**
       * {status} 订单状态 0/null全部，1：待付款，2：待发货，3：待收货，4：待评价，5：售后维权
       * {pageIndex} 页码
       * {pageSize} 每页条数，大小
       * {isLoading} 下啦刷新loading
       * {loading} 上啦加载更多的loading
       * {finished} 上啦加载完成
       * {channelId} 渠道，不传或者1为APP、H5，4为微商城小程序，5支付宝小程序
       * {commodityList} 商品收藏列表
       * {shopList} 店铺收藏列表
       * {windowHeight} 手机分辨率高度
       * {windowWidth} 手机分辨率宽度
       * {refreshHeight} 下啦刷新组建的高度
       */
      skeletonLoading: true,
      error: false,
      loading: false,
      finished: false,
      status: '0',
      pageIndex: 0,
      pageSize: 10,
      isLoading: false,
      isEdit: false, // 是否打开管理模式
      selectAllStatus: false, // 全选状态
      layoutType: false, // false 列表模式 true 大图模式
      stockType: 0, // 0:全部 1:有货 2:预购 3:无货
      activityTabs: [], // 活动标签
      stockNumber: {
        hasStockNum: 0,
        noStockNum: 0,
        preStockNum: 0,
        allStockNum: 0,

      },
      data: {
        channelId: 1,
        commodityList: [],
        shopList: [],
        // refreshHeight: 0,
      },
      windowHeight: 0,
      windowWidth: 0,
    }
  },
  computed: {
    ...mapGetters({
      location: 'getlocationInfo',
    }),
    shareId() {
      return sessionStorage.getItem('shareUserId') || '';
    },
  },
  watch: {
    // selectAllStatus: {
    //   handler(newValue) {
    //     this.data.commodityList.forEach(i => {
    //       this.$set(i, 'isCheckedGood', newValue)
    //     })
    //   },
    //   immediate: true,
    //   deep: true
    // },
    'data.commodityList': {
      handler(newValue) {
        this.selectAllStatus = this.data.commodityList.every(i => {
          return i.isCheckedGood === true
        })
      },
      immediate: true,
      deep: true
    },
  },
  created() {
    // this.getSelectBoxTagList()
    // const context = this;
    // context.windowWidth = window.screen.width;
    // context.windowHeight = window.screen.height;
    // context.data.refreshHeight = context.windowHeight - 1.08 * 100 * context.windowWidth / 750;
  },
  activated() {
    const context = this;
    context.pageIndex = 0;
    // this.getSelectBoxTagList()
    // 判断登陆状态，未登录便登陆
    if (context.$util.isHaierApp()) { // 智家
      if (!this.$store.state.isLogin) {
        // this.$store.dispatch('doLogin')
        this.$store.dispatch('handleUpAuth').then(res => {
          if (res) {
            context.isLoading = true;
            context.data.commodityList = [];
            context.data.shopList = [];
            context.getList();
          } else {
            this.$store.dispatch('doLogin')
          }
        })
      } else {
        context.isLoading = true;
        context.data.commodityList = [];
        context.data.shopList = [];
        context.getList();
      }
    } else {
      /**
       * @description  我的收藏列表
      */
      context.isLoading = true;
      context.data.commodityList = [];
      context.data.shopList = [];
      context.getList();
    }
  },
  methods: {
    /**
     * 获取活动标签
     */
    // getSelectBoxTagList() {
    //   getSelectBoxTagList().then(res => {
    //     let result = res.data;
    //     this.activityTabs = result || []
    //   })
    // },
    /**
     * @description 切换大图/列表模式
     */
    layoutChange() {
      this.layoutType = !this.layoutType
    },
    chooseStock(val) {
      console.log('chooseStock:', val);
      switch (val) {
        case '全部':
          this.stockType = 0
          break;
        case '有货':
          this.stockType = 1
          break;
        case '无货':
          this.stockType = 3
          break;
        case '预订':
          this.stockType = 2
          break;
      }
      this.getList()
    },
    /**
     * @description 商品批量切换选中
     */
    toggleSelectAll() {
      this.data.commodityList.forEach(i => {
        this.$set(i, 'isCheckedGood', this.selectAllStatus)
      })
    },

    /**
     * @description 删除已收藏的商品
     */
    cancelCollection(item) {
      const context = this;
      const data = {
        sku: item.sku
      };
      personalApi.cancelProductCollection(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          context.isLoading = true;
          context.onRefresh(); // 下啦刷新
        } else {
          Toast.fail(_resData.message)
        }
      })
    },

    /**
     * @description 删除已收藏的店铺
     * @param {type} 收藏类型（1、店铺，2场馆）
     */
    cancelShop(item) {
      console.log('item', item);
      const context = this;
      const data = {
        collectId: item.collectId,
        type: 1,
      };
      personalApi.collectStore(data).then(res => {
        const _resData = res.data;
        if (_resData.success) {
          context.isLoading = true;
          context.onRefresh(); // 下啦刷新
        } else {
          Toast.fail(_resData.message)
        }
      })
    },

    /**
     * @description 下拉刷新,下啦刷新同时清空现有数据，页码初始化0
     * @param {commodityList} 商品收藏列表
     * @param {shopList} 店铺收藏列表
     */
    onRefresh() {
      const context = this;
      context.pageIndex = 0;
      // context.data.shopList = [];
      // context.data.commodityList = [];
      /**
       * @description  个人中心 我的收藏列表
      */
      context.onLoad();
    },

    /**
     * 页面上拉触底事件的处理函数，加载更多
     */
    onLoad() {
      const context = this;
      /**
       * @description 我的收藏列表
      */
      context.getList();
    },

    /**
     * @description 我的收藏列表
     * @param {channelId} 渠道，不传或者1为APP、H5，4为微商城小程序，5支付宝小程序
     * @param {commodityList} 商品收藏列表
     * @param {shopList} 店铺收藏列表
     * @param {type} 1店铺  2场馆  不传是店铺+场馆
     * 返回接口请求数据
    */
    getList () {
      const context = this;
      if (context.status === '0') {
        // 商品收藏列表
        const data = {
          shareId: this.shareId,
          channelId: context.data.channelId,
          cityId: context.location.cityId,
          provinceId: context.location.provinceId,
          districtId: context.location.districtId,
          streetId: context.location.townshipId,
          stockType: this.stockType,
        }
        personalApi.myCollectionList(data).then(res => {
          const _resData = res.data;
          this.skeletonLoading = false
          // 手动清除 Toast
          Toast.clear();
          if (_resData.success) {
            this.stockNumber.hasStockNum = _resData.data.hasStockNum
            this.stockNumber.noStockNum = _resData.data.noStockNum
            this.stockNumber.preStockNum = _resData.data.preStockNum
            this.stockNumber.allStockNum = Number(this.stockNumber.hasStockNum) + Number(this.stockNumber.noStockNum) + Number(this.stockNumber.preStockNum)

            context.data.commodityList = _resData.data.productsList || [];

            context.data.commodityList.forEach(item => {
              item.finalPrice = item.lowestFlashPrice || item.finalPrice
              item.isLowestFlash = !!item.lowestFlashPrice
              // 活动标签 处理逻辑
              this.activityTabs.forEach(_item => {
                if (_item.id === item.actTagId) {
                  item.productActNew = _item.tagName
                  item.productActTerm = ''
                  item.hasTag = true
                }
              })
            })
            // 加载状态结束
            context.finished = false;
            context.loading = false;

            // 下啦刷新完成
            if (context.isLoading) {
              context.isLoading = false;
            }
          } else {
            Toast.fail(_resData.message)
          }
        })
      } else {
        context.pageIndex = context.pageIndex + 1;
        // 店铺收藏列表
        const data = {
          page: context.pageIndex,
          pageSize: context.pageSize,
          type: 1,
        }
        personalApi.storeCollectionList(data).then(res => {
          const _resData = res.data;
          this.skeletonLoading = false
          // 手动清除 Toast
          Toast.clear();
          if (_resData.success) {
            // 下啦刷新完成
            if (context.isLoading) {
              context.data.shopList = [];
              context.isLoading = false;
            }

            context.data.shopList = context.data.shopList.concat(_resData.data);

            // 加载状态结束
            context.finished = false;
            context.loading = false;
            context.finished = false;

            // 数据全部加载完成
            if (_resData.data.length === 0 || _resData.totalCount < context.pageSize) {
              context.finished = true;
            }
            console.log(context.isLoading, context.finished);
          } else {
            Toast.fail(_resData.message)
          }
        })
      }
    },

    /**
     * @description  团队tab标签切换内容
     * @param {e} event
     * @param {isLoading} 下啦刷新的loading
     * 返回团队内容切换隐藏
    */
    switchTabChange(e) {
      const _checkedId = e.target.id;
      const context = this;
      context.status = _checkedId;
      context.isLoading = true;
      // context.data.commodityList = [];
      // context.data.shopList = [];
      if (_checkedId === '1') {
        this.isEdit = false
      }
      /**
       * @description 下拉刷新,下啦刷新同时清空现有数据，页码初始化0
       */
      context.onRefresh();
    },
    /**
       * @description 批量取消收藏
       */
    deleteMulti() {
      let deleteList = this.data.commodityList.filter(i => {
        return i.isCheckedGood
      })
      let skuList = []
      deleteList.forEach(i => {
        skuList.push(i.sku)
      })
      personalApi.cancelCollection({
        skus: skuList
      }).then(res => {
        if (res.data.success) {
          this.getList()
          this.isEdit = false
        }
      })
    }

  },
}
</script>

