<template>
  <div class="filter-container" :class="{'radius':!dissountPop && !stockPop}">
       <!-- 筛选栏 -->
          <div class="left">
            <ul>
              <li :class="{ 'filter-active': true }" @click="reset()">
                <div class="text">默认</div>
                <div class="icon">
                </div>
              </li>
                 <!-- <li :class="{ 'filter-active': currentFilter === 'dissount' }" @click="changeFilter('dissount')">
                <div class="text">优惠</div>
                <div class="icon">
                <span class="icon-down"></span>
                </div>
              </li> -->
                <li :class="{ 'filter-active': currentFilter === 'stock' }" @click="changeFilter('stock')">
                <div class="text">库存</div>
                <div class="icon">
                  <span class="icon-down"></span>
                </div>
              </li>
              <li>
                <div class="layoutImg" @click="layoutChange">
                  <img v-if="layoutType" alt :src="`${$cdnImageUrl}/search/layout_new.png` | imageToHttps" />
                  <img v-else alt :src="`${$cdnImageUrl}/search/layoutSingle_new.png` | imageToHttps" />
                </div>
              </li>
            </ul>
          </div>
          <common-popup v-model="dissountPop" @bgClicked="bgClicked">
            <div class="filter-item-container">
              <ul>
                <li :class="{'item': true,'item-active':dissountItemSelect==='全部'} " @click="choosedissount('全部')"><span class="checked-icon"></span>全部(139)</li>
                <li :class="{'item': true,'item-active':dissountItemSelect==='有优惠券'} " @click="choosedissount('有优惠券')"><span class="checked-icon"></span>有优惠券(68)</li>
                <li :class="{'item': true,'item-active':dissountItemSelect==='无优惠券'} " @click="choosedissount('无优惠券')"><span class="checked-icon"></span>无优惠券(71)</li>
              </ul>
            </div>
          </common-popup>
          <common-popup v-model="stockPop" @bgClicked="bgClicked">
             <div class="filter-item-container">
              <ul>
                <li :class="{'item': true,'item-active':stockItemSelect==='全部'} " @click="chooseStock('全部')"><span class="checked-icon"></span>全部({{parentData.allStockNum}})</li>
                <li :class="{'item': true,'item-active':stockItemSelect==='有货'} " @click="chooseStock('有货')"><span class="checked-icon"></span>有货({{parentData.hasStockNum}})</li>
                <li :class="{'item': true,'item-active':stockItemSelect==='无货'} " @click="chooseStock('无货')"><span class="checked-icon"></span>无货({{parentData.noStockNum}})</li>
                <li :class="{'item': true,'item-active':stockItemSelect==='预订'} " @click="chooseStock('预订')"><span class="checked-icon"></span>预订({{parentData.preStockNum}})</li>

              </ul>
            </div>
          </common-popup>

  </div>
</template>

<script>
import commonPopup from './common-popup';
export default {
  name: 'commodityFilter',
  components: {
    commonPopup
  },
  props: {
    /**
     * 父组件传入数据源
     */
    parentData: {
      type: null,
      default: function() {
        return {}
      },
    }
  },
  data() {
    return {
      currentFilter: 'default',
      layoutType: false, // false 列表模式 true 大图模式
      dissountPop: false, // 优惠弹窗
      stockPop: false, // 库存弹窗
      dissountItemSelect: '全部',
      stockItemSelect: '全部'

    }
  },
  methods: {
    /**
     * @description 切换大图/列表模式
     */
    layoutChange() {
      this.layoutType = !this.layoutType
      this.stockPop = false
      this.$emit('layoutChange')
    },
    /**
     * @description 优惠筛选
     */
    choosedissount(val) {
      this.dissountItemSelect = val
    },
    /**
     * @description 库存筛选
     */
    chooseStock(val) {
      this.stockItemSelect = val
      this.stockPop = false
      this.$emit('chooseStock', val)
    },
    // 默认筛选
    reset() {
      this.stockItemSelect = '全部'
      this.stockPop = false
      this.$emit('chooseStock', '全部')
    },
    // 切换筛选条件
    changeFilter(v) {
      switch (v) {
        case 'dissount':
          this.currentFilter === 'dissount' ? this.currentFilter = '' : this.currentFilter = 'dissount'
          this.stockPop = false
          this.dissountPop = !this.dissountPop
          break;
        case 'stock':
          this.currentFilter === 'stock' ? this.currentFilter = '' : this.currentFilter = 'stock'
          this.dissountPop = false
          this.stockPop = !this.stockPop
          break;
      }
    },
    bgClicked() {
      this.dissountPop = false
      this.stockPop = false
      this.currentFilter = ''
    }
  }
}
</script>

